import * as React from "react";
import Lottie from "lottie-react";
import {useOnScreen} from "../../../hooks/IsInViewport";
import { Script, ScriptStrategy } from "gatsby";

const lottieOptions = {
    loop: true,
    autoplay: false,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
    }
};

const experimental = () => {
    return (
        <>
                   <Script
                src="https://cdnjs.cloudflare.com/ajax/libs/lottie-web/5.10.2/lottie.min.js"
                strategy={ScriptStrategy.offMainThread}>
            </Script>

            <Script id="lottie-config" strategy="off-main-thread" forward={[`lottie`]}>
            </Script>
 
        </>
    );
};
export const LottieWrapper = (props) => {
    const containerRef = React.useRef(),
        lottieRef = React.useRef(),
        onScreen = useOnScreen(containerRef),
        {animationData, style} = props;

    React.useEffect(() => {
        if (onScreen) {
            lottieRef.current.play();
        } else {
            lottieRef.current.pause();
        }
    }, [onScreen]);

    const options = {
        animationData,
        ...{
            ...lottieOptions,
            autoplay: onScreen,
        },
    };

    return (
        <div ref={containerRef}>

            <Lottie
                lottieRef={lottieRef} 
                className="LottieAnimation"
                {...options}
                style={style}
                {...props}
            />
        </div>
    );
};

export default LottieWrapper;